import React from 'react'
import {Link} from 'gatsby'
import SocialLinkFP from './AgencyAgentFPFooter.component'
const AgentListItem=({addClass,ListView,children,name,office,fax,mobile,email,facebook,twitter,instagram,pinterest,dribbble,google,Havelisting})=>{
    return(
        
        <div className={
        ListView ? 
        "feat_property list agent "+ addClass
        :
        "feat_property home7 agent"
        }>
       
            <div className="thumb">
             <Link to="/agent/single">{children}</Link>
                <div className="thmb_cntnt">
                    <ul className="tag mb0">
                        <li className="list-inline-item dn"></li>
                        <li className="list-inline-item"><span>2 Listings</span></li>
                    </ul>
                </div>
            </div>
            <div className="details">
            <Link to="/agent/single">
                <div className="tc_content">
                    <h4>{name}</h4>
                    <p className="text-thm">Agent</p>
                    <ul className="prop_details mb0">
                        <li><span>Office: {office}</span></li>
                        <li><span>Mobile: {mobile}</span></li>
                        <li><span>Fax: {fax}</span></li>
                        <li><span>Email: {email}</span></li>
                    </ul>
                </div>
                </Link>
                <div className="fp_footer">
                    <SocialLinkFP 
                        facebook={facebook}
                        twitter={twitter}
                        instagram={instagram}
                        pinterest={pinterest}
                        dribbble={dribbble}
                        google={google}
                    />
                    {Havelisting ?<Link to="/list"><div className="fp_pdate float-right text-thm">View My Listings <i className="fa fa-angle-right"></i></div></Link> 
                    
                    :
                    <div className="fp_pdate float-right text-thm">4 years ago</div>
                    }
                    
                </div>
            </div>
        </div>
    
    )
}
export default AgentListItem