import React from 'react'
import ReactSelect from '../../../App/ReactSelect/ReactSelect'
import {categories,cities} from '../../../App/ReactSelect/SelectData'

function FindAgent() {
    return (
        <div className="sidebar_advanced_search_widget">
        <h4 className="mb25">Find Agent</h4>
        <ul className="sasw_list mb0">
            <li className="search_area">
                <div className="form-group">
                    <input type="text" className="form-control" id="exampleInputName1" placeholder="Enter Agent Name"/>
                </div>
            </li>
            <li>
                <div className="search_option_two">
                    <div className="candidate_revew_select">
                    <ReactSelect data={categories} />
                    </div>
                </div>
            </li>
            <li>
                <div className="search_option_two">
                    <div className="candidate_revew_select">
                        <ReactSelect data={cities} />
                    </div>
                </div>
            </li>
            <li>
                <div className="search_option_button">
                    <button type="submit" className="btn btn-block btn-thm">Search</button>
                </div>
            </li>
        </ul>
    </div>
    )
}

export default FindAgent
